import React, { Component } from 'react';

const API = 'https://api.summrize.com/api/';
const DEFAULT_QUERY = 'day';

class Summrize extends Component {

    constructor(props) {
        super(props);
    
        this.state = {
            data:null,
          renderedData:null,
        }
      }
      

    componentDidMount() {
        return  fetch(API + DEFAULT_QUERY)
           .then(response => response.json())
           .then((responseJson) => {
            //('mornin')
            console.log(responseJson)
            const dayJson = [...new Map(responseJson.map(o => [o.day._id, o.day])).values()].reverse()
            console.log(dayJson)
            this.setState({
            isLoading: false,
            dataSource: dayJson,
          })
            this.setState({renderedData:(
                      dayJson.map((item, index) => {
                        return (
                            <div key={`${item._id}`}>
                            <hr></hr>
                                <a href={item.url}><img src={this._handleEmptyStringUri(item)} style={this._handleEmtypImage(item)}></img></a>
                                <h2 style={this._titleStyle()}>{item.title}</h2>
                                <a href={item.url}><p style={this._articleUrl()} >Read Article</p></a>

                            </div>
                        );
                    })
                )}) 
                return 
        })
       }


    render(){
const {renderedData} = this.state;

        return (
            <div>{renderedData}</div>

        );
    }

    _handleEmtypImage(item) {
        //console.log(item)
 
              if (!item.article_thumbnail && !item.article) {
                  //console.log("what" + item.mornin.article)
                  return {
                    backgroundColor: '#d4f5f5',
                    height:0,
                  }
                }
                else {
                  return {
                    marginTop:20,
                    height: 250,
                    borderRadius: 25,
                    overflow:'hidden',
                    width: '75%',
                    marginLeft: 'auto',
                    marginRight: 'auto',
                  }
                }
        }
        _handleEmptyStringUri(item) {

              if (!item.article_thumbnail){
                //console.log("blah" + item.mornin.article_thumbnail)
                return item.article
              }
              else {
                return item.article_thumbnail
              }
          }
          _lineStyle(){
              return {
                borderBottomWidth: 1,
                borderColor:'gray',
                margin:10,
                backgroundColor: 'black',
              }
          }
          _titleStyle() {
            return {
                width:'75%',
                paddingTop:0,
                paddingBottom: 10,
                color: '#3e6990',
                fontSize: 20,
                textAlign: 'center',
                marginHorizontal:5,
                justifyContent: 'center',
                alignItems: 'center',
                borderRadius:10,
                overflow:'hidden',
                marginLeft: 'auto',
                marginRight: 'auto',
              }
        }
        _articleUrl() {
            return {
                marginBottom: 20,
                color: 'rgba(255,166,158, 1)',
                fontSize: 14,
                textAlign: 'center',
                backgroundColor: 'transparent',
                fontWeight: 'bold'
              }
        }
}


export default Summrize;